uploadDataMethods.uploadDesignationFile = async info => {
  try {
    store.dispatch(showLoader(true));
    let formData = new FormData();
    formData.append("file", info.file);
    formData.append("empId", getEmployeeIdFromStore());
    const response = await designationService.uploadDocument(formData);
    if (response?.statusCode === 200) {
      message.warning(response?.message);
    } else {
      store.dispatch(showLoader(false));
      message.error(response?.message);
    }
  } catch (error) {
    console.error(error);
  } finally {
    store.dispatch(showLoader(false));
  }
};
uploadDataMethods.uploadDomainUpload = async info => {
  try {
    store.dispatch(showLoader(true));
    let formData = new FormData();
    formData.append("empId", getEmployeeIdFromStore());
    formData.append("file", info.file);
    const response = await domainService.uploadDocument(formData);
    if (response?.statusCode === 200) {
      message.warning(response?.message);
    }
  } catch (error) {
    console.error(error);
  } finally {
    store.dispatch(showLoader(false));
  }
};

uploadDataMethods.uploadEmployeeResume = async (info, empId) => {
  let size = info.file.size / 1024 / 1024 < 3;
  if (!size) return message.error("File must smaller than 3MB!");
  try {
    let formData = new FormData();
    formData.append("file", info?.file);
    formData.append("docType", "profile");
    formData.append("empId", empId);
    formData.append("docTitle", info?.docTitle);
    const response = await employeeService.uploadEmployeeResume(formData);
    if (response?.statusCode === 200) {
      message.success(response?.message);
    } else {
      // message.error(response?.message);
    }
  } catch (error) {
    console.error(error);
  } finally {
  }
};

uploadDataMethods.downloadEmployeeResume = async empId => {
  try {
    const response = await employeeService.downloadEmployeeResume({ empId, docType: "profile" });
    response?.downloadLink && window.open(response.downloadLink, "_blank");
  } catch (err) {
    message.error(err);
    console.error(err);
  }
};
