import { Card, Col, Modal, PageHeader, Row, Tooltip } from "antd";
import React, { useEffect, useState } from "react";
import employeeResumeService from "services/EmployeeResumeService";
import { DeleteOutlined, EyeOutlined, DownloadOutlined } from "@ant-design/icons";
import { useDispatch } from "react-redux";
import { showLoader } from "redux/actions/Loader";
import { CONFIRM_DELETE_MESSAGE } from "constants/CommonConstants";

const { Meta } = Card;
function ResumeComponent({ empId, setFetchDocuments, fetchDocuments, showTitle = false }) {
  const [visible, setVisible] = useState(false);
  const [docs, setDocs] = useState([]);
  const [docId, setDocId] = useState();
  const dispatch = useDispatch();
  const { confirm } = Modal;

  useEffect(() => {
    if (empId && fetchDocuments) {
      getDocsListById(empId);
    }
  }, [empId, fetchDocuments]);

  const getDocsListById = async id => {
    let resp = await employeeResumeService.getResumeByEmpId(id);
    setDocs(resp);
    setFetchDocuments(false);
  };

  const onDeleteClick = async params => {
    let obj = {
      fileName: params?.fileName,
      empId: params?.empId,
      userEmail: params?.createdBy,
    };
    dispatch(showLoader(true));
    let resp = await employeeResumeService.deleteResumeByEmpId(obj);
    if (resp?.empId) {
      await getDocsListById(resp?.empId);
    }
    dispatch(showLoader(false));
  };
  const handleConfirm = params => {
    confirm({
      title: CONFIRM_DELETE_MESSAGE.value,
      centered: true,
      onOk() {
        onDeleteClick(params);
      },
    });
  };
  const onDownloadClick = async params => {
    let obj = {
      empId: params?.empId,
      fileName: params?.fileName,
    };
    let resp = await employeeResumeService.downloadResumeByEmpId(obj);
    if (resp) {
      const link = document.createElement("a");
      link.download = resp;
      link.href = resp;
      link.click();
    }
  };

  return (
    <>
      {showTitle && (
        <Row align="middle" gutter={[20, 20]} className="my-0">
          <Col xs={24} md={24} xl={6}>
            <PageHeader
              className="p-2 pl-2"
              title={"Documents"}
              // subTitle={
              //   <Button className="ml-2" onClick={handleRefresh}>
              //     <SyncOutlined shape="circle" />
              //   </Button>
              // }
            />
          </Col>
        </Row>
      )}
      <Row className="mx-3 mb-2">
        {!!docs.length ? (
          docs.map(item => {
            return (
              <Card
                hoverable
                className="mr-2"
                style={{ width: 150 }}
                cover={
                  <img
                    className="mt-4"
                    height="100%"
                    style={{ width: 50, display: "flex", margin: `auto` }}
                    alt="example"
                    src="https://icon-library.com/images/document-icon-image/document-icon-image-27.jpg"
                  />
                }
                actions={[
                  <Tooltip title="Download File">
                    <DownloadOutlined key="View" onClick={() => onDownloadClick(item)} />
                  </Tooltip>,
                  // <Tooltip title="View File">
                  //   <EyeOutlined key="View" onClick={()=>{setVisible(!visible);setDocId(item?.fileName)}} />
                  // </Tooltip>,
                  <Tooltip title="Delete File">
                    <DeleteOutlined style={{ color: "red" }} key="Delete" onClick={() => handleConfirm(item)} />
                  </Tooltip>,
                ]}
              >
                <Meta align="center" title={item?.docTitle} />
              </Card>
            );
          })
        ) : (
          <span>No Document Available</span>
        )}
      </Row>
      <Modal
        title={docs?.filter(e => e?.fileName === docId)[0]?.docTitle}
        visible={visible}
        onOk={() => setVisible(!visible)}
        onCancel={() => setVisible(!visible)}
      >
        {/* <iframe src={"} /> */}
      </Modal>
    </>
  );
}

export default ResumeComponent;
