import { AUTH_TOKEN } from "redux/constants/Auth";
import Utils from "../utils/index";
import API_CONSTANTS from "constants/ApiConstant";
import service from "auth/FetchInterceptor";
import store from "redux/store";
import { get } from "auth/FetchApiInterceptor";
const getEmployeeIdFromStore = () => {
  return store.getState().myTeams.loggedinId;
};
const paycomService = {};

paycomService.uploadFile = async file => {
  const res = await get(API_CONSTANTS.UPLOAD_PAYCOM, file);
  return res;
};

paycomService.updateSingleRecords = async function (data) {
  const { path, method } = API_CONSTANTS.UPDATE_PAYCOM_RECORD;
  try {
    const response = await service({
      url: `${path}${getEmployeeIdFromStore()}`,
      method,
      data,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response.records;
  } catch (e) {
    console.error("Error in getting the employee by id list.", e);
  }
};
export default paycomService;
