import { DownloadOutlined, UploadOutlined } from "@ant-design/icons";
import { Button, Col, Form, Modal, Row, Select, Upload } from "antd";
import {
  ACCEPT_FILE_LIST,
  CONFIRM_MESSAGE,
  MODAL_ADD_BUTTON_TYPE,
  SAMPLE_DOMAIN_DATA_FILE,
  SAMPLE_PAYCOM_SYNC_DATA_FILE,
  SAMPLE_PROJECT_DATA_FILE,
  SCREEN_ITEMS_TYPES,
  SUBMIT_TEXT,
} from "constants/CommonConstants";
import useIntlMessage from "hooks/useIntlMessage";
import { useState } from "react";
import uploadDataMethods from "../../utils/UploadDataFunctions";
import { CANCEL_TEXT, VALIDATION_FAILED } from "../../constants/AddSubDomainConstants";
import { SAMPLE_EMPLOYEE_DATA_FILE, SAMPLE_DESIGNATION_DATA_FILE } from "../../constants/CommonConstants";
import { useEffect } from "react";
import employeeResumeService from "services/EmployeeResumeService";

const UploadData = ({
  visible,
  onCancel,
  sampleFile,
  screen,
  onResponse,
  getProjectResponse,
  empId,
  setFetchDocuments,
  onEmployeeResponse,
}) => {
  const [file, setFile] = useState(null);
  const [form] = Form.useForm();
  const [docTitles, setDocTitles] = useState([]);
  const getMessage = useIntlMessage();
  const { confirm } = Modal;
  const { Option } = Select;

  useEffect(() => {
    getDocumentTitle();
  }, []);
  const handleConfirm = () => {
    confirm({
      title: CONFIRM_MESSAGE.value,
      centered: true,
      onOk() {
        onCancel();
      },
    });
  };

  const getDocumentTitle = async () => {
    let docTitles = await employeeResumeService.getDocTitle();
    setDocTitles(docTitles);
  };

  const Cancel = () => {
    if (form.isFieldsTouched()) {
      handleConfirm();
    } else {
      onCancel();
    }
  };

  const handleOk = () => {
    form
      .validateFields()
      .then(value => {
        onMenuItemClick(screen, value);
        onCancel();
      })
      .catch(info => {
        console.log(VALIDATION_FAILED.value, info);
      });
  };

  const handleFileUpload = async info => {
    if (info.fileList.length > 0) {
      setFile(info);
    }
  };
  const onDeleteFile = async info => {
    await setFile(null);
  };
  const getDownloadedFileName = key => {
    switch (key) {
      case SCREEN_ITEMS_TYPES.employee:
        return SAMPLE_EMPLOYEE_DATA_FILE;
      case SCREEN_ITEMS_TYPES.designation:
        return SAMPLE_DESIGNATION_DATA_FILE;
      case SCREEN_ITEMS_TYPES.domain:
        return SAMPLE_DOMAIN_DATA_FILE;
      case SCREEN_ITEMS_TYPES.paycomSync:
        return SAMPLE_PAYCOM_SYNC_DATA_FILE;
      case SCREEN_ITEMS_TYPES.uploadProject:
        return SAMPLE_PROJECT_DATA_FILE;
      default:
        return;
    }
  };
  const getModalName = key => {
    switch (key) {
      case SCREEN_ITEMS_TYPES.employee:
        return getMessage("common.uploademployee.tiltle");
      case SCREEN_ITEMS_TYPES.designation:
        return getMessage("common.uploaddesignation.tiltle");
      case SCREEN_ITEMS_TYPES.domain:
        return getMessage("common.upload.domain");
      case SCREEN_ITEMS_TYPES.paycomSync:
        return getMessage("sidenav.setup.paycom");
      case SCREEN_ITEMS_TYPES.uploadProject:
        return getMessage("sidenav.setup.uploadProject");
      case SCREEN_ITEMS_TYPES.uploadResume:
        return getMessage("common.upload.resume");
      default:
        return;
    }
  };
  const onMenuItemClick = async (key, value) => {
    switch (key) {
      case SCREEN_ITEMS_TYPES.employee:
        const resp = await uploadDataMethods.uploadEmployeeBulkDataFile(file);
        resp && onEmployeeResponse(resp);
        break;
      case SCREEN_ITEMS_TYPES.designation:
        uploadDataMethods.uploadDesignationFile(file);
        break;
      case SCREEN_ITEMS_TYPES.domain:
        uploadDataMethods.uploadDomainUpload(file);
        break;
      case SCREEN_ITEMS_TYPES.paycomSync:
        const res = await uploadDataMethods.uploadPaycomFile(file);
        onResponse(res);
        break;
      case SCREEN_ITEMS_TYPES.uploadProject:
        const response = await uploadDataMethods.uploadProjectFile(file);
        response && getProjectResponse(response.body);
        break;
      case SCREEN_ITEMS_TYPES.uploadResume:
        let info = {
          file: file.file,
          docTitle: value.docTitle,
        };
        await uploadDataMethods.uploadEmployeeResume(info, empId);
        setFetchDocuments(true);
        break;
      default:
        return;
    }
  };
  return (
    <Modal
      visible={visible}
      title={getModalName(screen)}
      onCancel={Cancel}
      onOk={handleOk}
      footer={[
        <Button onClick={Cancel}>{CANCEL_TEXT.text}</Button>,
        <Button type={MODAL_ADD_BUTTON_TYPE.value} key={SUBMIT_TEXT.value} onClick={handleOk} disabled={file == null}>
          {getMessage("common.upload.titile")}
        </Button>,
      ]}
    >
      {screen !== SCREEN_ITEMS_TYPES.uploadResume && (
        <div className="mb-5 text-center">
          <span className="downlaod-button">
            <DownloadOutlined className="mr-1" />
            <a href={sampleFile} target="_blank" download={getDownloadedFileName(screen)} rel="noreferrer">
              {getMessage("components.dataEntry.sample.File")}
            </a>
          </span>
        </div>
      )}
      {/* <div className="mb-5 text-center">
        <span className="downlaod-button">
          <DownloadOutlined className="mr-1" />
          <a href={sampleFile} target="_blank" download={getDownloadedFileName(screen)} rel="noreferrer">
            {getMessage("components.dataEntry.sample.File")}
          </a>
        </span>
      </div> */}
      <Form form={form} onFinish={handleOk} layout={"horizontal"}>
        {screen === SCREEN_ITEMS_TYPES.uploadResume && (
          <Row gutter={24}>
            <Col className="mb-1" lg={24}>
              <Form.Item label="Select Document Title" name={"docTitle"} rules={[{ required: true }]}>
                <Select placeholder={"Selec Document Title"}>
                  {!!docTitles.length &&
                    docTitles.map(title => {
                      return (
                        <Option value={title} key={title}>
                          {title}
                        </Option>
                      );
                    })}
                </Select>
              </Form.Item>
            </Col>
          </Row>
        )}
        <Row gutter={24} align="center">
          <Form.Item className="text-center">
            <Upload
              maxCount={1}
              multiple={false}
              beforeUpload={() => false}
              showUploadList={true}
              onChange={handleFileUpload}
              onRemove={onDeleteFile}
              accept={screen === SCREEN_ITEMS_TYPES.uploadResume ? ".doc,.docx,.pdf" : ACCEPT_FILE_LIST}
              listType="picture"
            >
              <Button className="ant-btn mr-5 w-100" icon={<UploadOutlined className="mr-2" />}>
                {getMessage("components.dataEntry.Choose.File")}
              </Button>
            </Upload>
          </Form.Item>
        </Row>
      </Form>
    </Modal>
  );
};

export default UploadData;
