projectService.getList = async function (params) {
  const { path, method } = API_CONSTANTS.GET_PROJECT_LIST;
  if (getEmployeeIdFromStore()) {
    try {
      const response = await service({
        url: `${path}${getEmployeeIdFromStore()}`,
        method,
        params,
      });
      if (Utils.isEmpty(response)) {
        return [];
      }
      return response.records;
    } catch (e) {
      console.error("Error in getting the domain list.", e);
    }
  }
};

