projectService.engageProjectResource = async function (employeeData) {
  const payload = createPayloadForEngageAndReleaseResource(employeeData);
  const response = await projectService.updateResourceToProject({ ...payload });
  if (response.statusCode === 801) {
    const resp = await projectService.updateResourceToProject({ ...payload, status: "Engaged" });
    return resp;
  }
};

