projectService.prepareEmployeeReleaseData = (values, selectedResource) => {
  return {
    employeeId: selectedResource?.employeeId,
    accountId: selectedResource?.reservedFor?.accountId
      ? selectedResource?.reservedFor?.accountId
      : selectedResource?.parentAccount?.accountId,
    assignedLevel: selectedResource?.assignedLevel,
    feedback: values?.comments,
    releaseFeedbackReason: values?.releaseFeedbackReason,
    startDate: values?.startDate ? moment(values?.startDate).format(NEW_DATE_FORMAT).valueOf() : null,
    notes: values.notes,
  };
};
projectService.prepareAddAllocationPayload = (values, selectedResource) => ({
  employeeId: selectedResource?.employeeId,
  projectId: values?.projectId,
  domainId: values?.domainId,
  subDomainId: values?.subDomainId,
  reservedForAccountId: selectedResource?.reservedFor?.accountId,
  allocationStatus: values?.allocationStatus,
  assignedLevel: selectedResource?.assignedLevel,
  startDate: values?.startDate ? moment(values?.startDate).format(NEW_DATE_FORMAT).valueOf() : null,
  futureAllocationReleaseDate: values?.futureAllocationReleaseDate
    ? moment(values?.futureAllocationReleaseDate).format(NEW_DATE_FORMAT).valueOf()
    : null,
  notes: values.notes,
  nextAllocationStartDate: values?.nextAllocationStartDate
    ? moment(values?.nextAllocationStartDate).format(NEW_DATE_FORMAT).valueOf()
    : null,
});

const prepareExtendData = data => {
  return {
    comments: data?.comments,
    accountId: data?.accountId,
    employeeId: data?.employeeId,
    notes: data?.notes,
  };
};

/** ------------------------------ */
/** PUT & POST Utilty method for creating payload for AddResourceToProject */
/** ------------------------------ */
projectService.createPayloadForAddProjectToResourceAPI = function (formData, selectedProject, selectedResource) {
  const [startDate, endDate] = formData?.billableDate;
  return {
    id: selectedResource?.id || "",
    employeeId: formData?.emp?.employeeId,
    onBehalfOf: formData?.onBehalf?.employeeId,
    projectId: selectedProject?.projectId,
    billableStatus: formData?.billableStatus,
    billingEndDate: new Date(endDate),
    resourceRole: formData?.assignedRole,
    billingStartDate: new Date(startDate),
    status: formData?.allocationStatus,
    subDomain: formData?.subdomain,
  };
};

const createPayloadForEngageAndReleaseResource = selectedResource => {
  return {
    id: selectedResource?.id || "",
    employeeId: selectedResource?.employeeId,
    onBehalfOf: selectedResource?.onBehalf,
    projectId: selectedResource?.projectId,
    billableStatus: selectedResource?.billableStatus,
    billingEndDate: new Date(moment(selectedResource?.billingEndDate)),
    resourceRole: selectedResource?.resourceRole,
    billingStartDate: new Date(moment(selectedResource?.billingStartDate)),
    status: selectedResource?.status,
    subDomain: selectedResource?.subDomain,
    domain: selectedResource?.domain,
  };
};

projectService.createPayloadForAPI = (formValues, accountsList, selectedProject) => {
  const { accountName, projectName, projectStatus, projectStartDate, projectEndDate } = formValues;
  const account = accountsList.find(x => x.accountId === accountName || x.accountName === accountName);
  return {
    projectId: selectedProject?.projectId || "",
    projectName,
    account: account?.accountName,
    accountId: account?.accountId,
    managerIds: [],
    status: projectStatus,
    projectStartDate: projectStartDate ? moment(projectStartDate).format(NEW_DATE_FORMAT).valueOf() : null,
    projectEndDate: projectEndDate ? moment(projectEndDate).format(NEW_DATE_FORMAT).valueOf() : null,
  };
};

const createPayloadForReleaseResourceAPI = selectedResource => {
  const { id, employeeId, projectId, billableStatus, billingEndDate, billingStartDate, resourceRole } =
    selectedResource;
  return {
    id,
    employeeId,
    projectId,
    billableStatus,
    billingEndDate,
    resourceRole,
    billingStartDate,
  };
};

