projectService.prepareAllocationEndDate = (field, record) => {
  let allocationDate = "";
  if (field.key === PROJECT_ALLOCATION_TABLE.PROJECT_START_DATE.key) {
    allocationDate = record[field.key] || "";
  } else {
    if (record["empActiveInd"]) {
      if (record[PROJECT_ALLOCATION_TABLE.ALLOCATION_SUB_STATUS.key] === SHADOW) {
        allocationDate = record[END_DATE_KEY.RESERVED_DATE] || "";
      } else if (record[PROJECT_ALLOCATION_TABLE.ALLOCATION_SUB_STATUS.key] === RESERVED) {
        allocationDate = record[END_DATE_KEY.EXTENDED_DATE]
          ? record[END_DATE_KEY.EXTENDED_DATE]
          : record[END_DATE_KEY.RESERVED_DATE];
      }
    } else {
      allocationDate = record[END_DATE_KEY.END_DATE];
    }
  }
  return allocationDate ? moment(allocationDate).format(DATE_FORMAT) : allocationDate;
};
projectService.flatRecords = record => {
  const data = record.map(item => {
    return {
      ...item,
      projectName: item?.project?.projectName,
      accountName: item?.accountData?.accountName,
      clientDeliveryHead: item?.clientDeliveryHead?.employeeName,
      shift: item?.shift,
      empActiveInd: item?.activeInd,
      activeInd: item?.activeInd ? ALLOCATION_STATUS.active : ALLOCATION_STATUS.InActive,
    };
  });
  return Utils.sortArrayByDate(data, "startDate");
};
