import service from "../auth/FetchInterceptor";
import API_CONSTANTS from "../constants/ApiConstant";
import Utils from "../utils";

const employeeResumeService = {};

employeeResumeService.getResumeByEmpId = async(id) => {
    const { path, method } = API_CONSTANTS.GET_EMPLOYEE_RESUME_BY_ID;
    try {
      const response = await service({
        url: `${path}?empId=${id}`,
        method,
      });
      if (Utils.isEmpty(response)) {
        return [];
      }
      return response?.records;
    } catch (e) {
      console.error("Error in getting the Employee Resume data.", e);
    }
}

employeeResumeService.getDocTitle = async() => {
    const { path, method } = API_CONSTANTS.GET_DOC_TITLE;
    try {
      const response = await service({
        url: path,
        method,
      });
      if (Utils.isEmpty(response)) {
        return [];
      }
      return response?.records;
    } catch (e) {
      console.error("Error in getting the Employee doc Title.", e);
    }
}

employeeResumeService.deleteResumeByEmpId = async(params) => {
    const { path, method } = API_CONSTANTS.DELETE_EMPLOYEE_RESUME_BY_ID;
    try {
      const response = await service({
        url: `${path}?fileName=${params?.fileName}&empId=${params?.empId}&userEmail=${params?.userEmail}`,
        method,
      });
      if (Utils.isEmpty(response)) {
        return [];
      }
      return response?.records;
    } catch (e) {
      console.error("Error in deleting the Employee Resume.", e);
    }
}

employeeResumeService.downloadResumeByEmpId = async(params) => {
    const { path, method } = API_CONSTANTS.DOWNLOAD_EMPLOYEE_RESUME_BY_ID;
    try {
      const response = await service({
        url: `${path}?empId=${params?.empId}&fileName=${params?.fileName}`,
        method,
      });
      if (Utils.isEmpty(response)) {
        return [];
      }
      return response?.downloadLink;
    } catch (e) {
      console.error("Error in downloading the Employee Resume.", e);
    }
}

export default employeeResumeService